<template>
  <div style="position:fixed;top:50px;bottom:60px;left:0;right:0;overflow-y:scroll;margin-top:10px;">
    <mt-navbar
      class="page-part"
      v-model="selected"
    >
      <mt-tab-item id="1">电表(kw-h)</mt-tab-item>
      <mt-tab-item id="2">水表(吨)</mt-tab-item>
      <mt-tab-item id="3">燃气(m³)</mt-tab-item>
    </mt-navbar>
    <mt-tab-container v-model="selected">
      <!-- 电表 -->
      <mt-tab-container-item
        id="1"
        class="bg_box"
      >
        <div>
          <section id="top">
            <div
              v-for="(center,i) in centerInfo"
              :key="i"
            >{{ center }}</div>
          </section>
        </div>
        <table
          v-for="(item,i) in focesInfo.filter(item=>item.elecMode != '2')"
          :key="i"
          style="width: 100%;"
        >
          <tr
            id="center"
            v-if="item.elecPrice != 0 && item.elecMode == 1"
          >
            <td>{{ item.roomInfo | splitRoomSn }}</td>
            <td>{{ item.preElecDegree }}</td>
            <td>
              <input
                class="input_box"
                placeholder="请输入"
                type="number"
                step="1"
                min="0"
                onkeyup="this.value= this.value.match(/\d+(\.\d{0,2})?/) ? this.value.match(/\d+(\.\d{0,2})?/)[0] : ''"
                v-model="item.elecDegree"
              />
            </td>
          </tr>
        </table>
      </mt-tab-container-item>
      <!-- 水表 -->
      <mt-tab-container-item
        id="2"
        class="bg_box"
      >
        <div>
          <section id="top">
            <div
              v-for="(center,i) in centerInfo"
              :key="i"
            >{{ center }}</div>
          </section>
        </div>
        <table
          v-for="(item,i) in focesInfo.filter(item=>item.waterMode != '2')"
          :key="i"
          style="width: 100%;"
        >
          <tr
            id="center"
            v-if="item.waterPrice != 0 && item.waterMode == 1"
          >
            <td>{{ item.roomInfo | splitRoomSn }}</td>
            <td>{{ item.preWaterDegree }}</td>
            <td>
              <input
                class="input_box"
                placeholder="请输入"
                type="number"
                step="1"
                min="0"
                onkeyup="this.value= this.value.match(/\d+(\.\d{0,2})?/) ? this.value.match(/\d+(\.\d{0,2})?/)[0] : ''"
                v-model="item.waterDegree"
              />
            </td>
          </tr>
        </table>
      </mt-tab-container-item>
      <!-- 燃气 -->
      <mt-tab-container-item
        id="3"
        class="bg_box"
      >
        <div>
          <section id="top">
            <div
              v-for="(center,i) in centerInfo"
              :key="i"
            >{{ center }}</div>
          </section>
        </div>
        <table
          v-for="(item,i) in focesInfo.filter(item=>item.gasMode != '2')"
          :key="i"
          style="width: 100%;"
        >
          <tr
            id="center"
            v-if="item.gasPrice != 0 && item.gasMode == 1"
          >
            <td>{{ item.roomInfo | splitRoomSn }}</td>
            <td>{{ item.preGasDegree }}</td>
            <td>
              <input
                class="input_box"
                placeholder="请输入"
                type="number"
                step="1"
                min="0"
                onkeyup="this.value= this.value.match(/\d+(\.\d{0,2})?/) ? this.value.match(/\d+(\.\d{0,2})?/)[0] : ''"
                v-model="item.gasDegree"
              />
            </td>
          </tr>
        </table>
      </mt-tab-container-item>
    </mt-tab-container>
    <mt-button
      class="button"
      id="aaa"
      @click="button_show = true"
    >提交抄表</mt-button>
    <mt-popup
      position="center"
      id="box_show"
      v-model="button_show"
    >
      <div>
        <p>提交抄表</p>
        <p>是否确认已完成水电抄表？</p>
        <div>
          <div @click="button_show = false">取消</div>
          <div @click="sure">确定</div>
        </div>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import { GetBusinessLifeCostBillBatchByhotel, PostBusinessLifeCostBillBatchDegreeAdd } from '@/api/api_business_hotel_and_scale';
import { splitRoomSn } from '@/utils/filters';
export default {
    data() {
        return {
            selected: '1',
            centerInfo: ['房间号', '上期读数', '本期读数'],
            focesInfo: [],
            button_show: false,
            ele: ''
        };
    },
    created() {
        this.$store.commit('setHeader', '集中抄表');
        this.getFocus();
    },
    methods: {
        getFocus() {
            GetBusinessLifeCostBillBatchByhotel({hotelId:this.$route.query.hotelId}).done((res) => {
                this.focesInfo = res.data.sort((a, b) => {
                    return splitRoomSn(a.roomInfo).localeCompare(splitRoomSn(b.roomInfo));
                });
            });
        },
        sure() {
            let arr = [];
            for (var i = 0; i < this.focesInfo.length; i++) {
                var obj = {};
                if (this.selected == 1) {
                    obj.id = this.focesInfo[i].id;
                    obj.degree = this.focesInfo[i].elecDegree;
                }
                if (this.selected == 2) {
                    obj.id = this.focesInfo[i].id;
                    obj.degree = this.focesInfo[i].waterDegree;
                }
                if (this.selected == 3) {
                    obj.id = this.focesInfo[i].id;
                    obj.degree = this.focesInfo[i].gasDegree;
                }
                arr.push(obj);
            }
            let aaa = arr.filter((item) => {
                return item.degree;
            });
            let data = {
                checkType: Number(this.selected),
                checkData: JSON.stringify(aaa)
            };
            PostBusinessLifeCostBillBatchDegreeAdd(data).done((res) => {
                this.getFocus();
            });
            this.button_show = false;
        }
    }
};
</script>
<style scoped>
.bg_box {
    background: #fff;
    margin-top: 5px;
}
#top {
    display: flex;
    justify-content: space-around;
    color: #888888;
    opacity: 0.7;
    font-size: 14px;
    line-height: 46px;
    border-bottom: 1px solid #efeff4;
}
#center {
    color: #888888;
    opacity: 0.7;
    font-size: 14px;
    line-height: 40px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #efeff4;
}
#center > :nth-child(1) {
    width: 30%;
    color: #000;
    font-weight: bold;
}
#center > :nth-child(2) {
    width: 30%;
}
#center > :nth-child(3) {
    width: 30%;
}
.input_box {
    color: #ff7d41;
    width: 50%;
    border: none;
    outline-style: none;
}
.button {
    background: #ff7d41;
    color: #fff;
    opacity: 0.9;
    font-size: 14px;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 45px;
}
.mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid #ff7d41;
    color: #ff7d41;
}
#box_show {
    width: 80%;
    border-radius: 6px;
}
#box_show > div > p:nth-child(1) {
    color: #313443;
    font-weight: bold;
    margin: 16px;
}
#box_show > div > p:nth-child(2) {
    color: #6a6a6a;
    font-size: 14px;
    line-height: 50px;
}
#box_show > div > div {
    display: flex;
    justify-content: space-around;
    line-height: 40px;
}
#box_show > div > div > div:nth-child(1) {
    color: #6a6a6a;
    font-size: 15px;
    border: 1px solid #e5e5e5;
    width: 50%;
    border-bottom-left-radius: 6px;
}
#box_show > div > div > div:nth-child(2) {
    color: #ff7d41;
    font-size: 15px;
    border: 1px solid #e5e5e5;
    width: 50%;
    border-bottom-right-radius: 6px;
}
</style>



